import ePub from 'epubjs';

const message = type => data => {
    return {
        type: type,
        data: data,
    }
}

class Orchestrator {
    constructor(socket, rendition) {
        this.socket = socket;
        this.rendition = rendition;
        this.handleOpen = this.handleOpen.bind(this);
        this.handleMessage = this.handleMessage.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleError = this.handleError(this);
        this.handleSelected = this.handleSelected(this);
        this.handleRelocated = this.handleRelocated(this);
    }

    init() {
        this.socket.onopen = this.handleOpen;
        this.socket.onmessage = this.handleMessage;
        this.socket.onclose = this.handleClose;
        this.socket.onerror = this.handleError;
        this.rendition.on('selected', this.handleSelected);
        this.rendition.on('relocated', this.handleRelocated);
    }

    handleOpen() {
        console.log('connection opened');
    }

    handleClose(event) {
        if (event.wasClean) {
            console.log(`connection closed, code=${event.code} reason=${event.reason}`);
        } else {
            console.error('connection died');
        }
    }

    handleError(error) {
        console.error(error.message);
    }

    handleMessage(event) {
        const message = JSON.parse(event.data)
        switch(message.type) {
            case 'relocated':
                this.handleRelocated(message.data.pageNumber);
                break;
            case 'highlighted':
                this.handleHightlight(message.data.cfiRange);
                break;
            default:
                console.log(`unrecognized message type of ${message.type}`);
        }
    }

    handleRelocated(pageNumber) {
        if (typeof pageNumber !== 'number') {
            this.handleError(new Error(`handleRelocated expected a number got ${typeof pageNumber}`));
        }
        this.rendition.display(pageNumber);
    }
    
    handleHightlight(cfiRange) {
        this.rendition.annotations.highlight(
            cfiRange,
            {},
            e => console.log(e)
        );
    }

    handleSelected(cfiRange) {
        const m = message('highlighted')(cfiRange);
        this.socket.send(JSON.stringify(m));
    }

    handleRelocated(location) {
        const pageNumber = location.start.location;
        const m = message('relocated')(pageNumber);
        this.socket.send(JSON.stringify(m));
    }
}

document.addEventListener('DOMContentLoaded', () => {
    const room = window.location.pathname.split("/").pop();
    const socket = new WebSocket(`ws://localhost:8080/ws/${room}`);
    const bookUri = '/public/childrens-literature.epub';
    const book = ePub(bookUri);
    const rendition = book.renderTo('viewer', {
        width: '100%',
        height: 400,
    })
    const o = new Orchestrator(socket, rendition);
    o.init()
})
